.banner-container {
  background-color: var(--primary-color);
}

.banner {
  display: flex;
  align-items: stretch;
  gap: 1rem;
  color: white;
  height: calc(100vh - var(--header-height));
  margin-top: var(--header-height);
  flex-direction: column;
}

.banner h1 {
  font-weight: normal;
}

.banner p {
  font-size: 1.5rem;
}

.banner > * {
  flex: 0 0 0;
  min-width: 0;
}

.banner-image {
  max-width: 100%;
  height: auto;
  border-radius: 0.5rem;
}

.beautiful-rule {
  border: none;
  height: 0.25rem;
  border-radius: 0.35rem;
  background: linear-gradient(
    90deg,
    white,
    rgba(255, 255, 255, 0.85),
    rgba(255, 255, 255, 0.65),
    transparent
  );
}

.header-text {
  font-size: 2rem;
}

@media screen and (min-width: 1024px) {
  .banner {
    flex-direction: row;
    align-items: center;
  }

  .banner h1 {
    font-size: 3rem;
  }

  .banner > * {
    flex: 1 0 0;
  }
}
