:root {
  box-sizing: border-box;
  --header-height: 4.5rem;
  --primary-color: #007ba7;
  --primary-color-lighten: #00a0da;
  --color-success: #00a72c;
  --accent-failure: #a72c00;
  --accent-color: #a7007b;
  /* consider a palette built around cerulean blue: https://www.schemecolor.com/cream-cerulean-blue.php */
  --text-color: #444;
  --menu-background: #222;
  color: var(--text-color);
}

input,
button {
  color: var(--text-color);
}

*,
*:after,
*:before {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.container {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
}

img {
  width: 100%;
}

@media screen and (min-width: 640px) {
  .container {
    max-width: 640px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
  }
}

@media screen and (min-width: 1024px) {
  .container {
    max-width: 1024px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    max-width: 1280px;
  }
}
