.authors {
  display: flex;
  flex-direction: column;
}

.author {
  padding: 0.5rem 0;
  text-align: left;
}

.filter-option {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 0.5rem;
  padding: 0.25rem 0.5rem;
  margin-top: 0.25rem;
  cursor: pointer;
}

.filter-option.checked {
  font-weight: bold;
}

.filter-option:hover {
  background-color: var(--primary-color);
  border-radius: 0.25rem;
  color: white;
}