.container {
  margin-left: 1rem;
  margin-right: 1rem;
}

img {
  width: 100%;
}

.head-spaced {
  margin-top: var(--header-height);
}

.search-container {
  composes: head-spaced container;
}

@media screen and (min-width: 640px) {
  .container {
    max-width: 640px;
    margin-left: auto;
    margin-right: auto;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
  }
}

@media screen and (min-width: 1024px) {
  .container {
    max-width: 1024px;
  }

  .search-link-text {
    display: none;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    max-width: 1280px;
  }
}
