.hero-button {
  font-size: 1.25rem;
  padding: 1rem;
  border-radius: 0.25rem;
  border: 1px solid white;
  background-color: transparent;
  color: white;
  cursor: pointer;
}

.hero-button:hover {
  background-color: var(--primary-color-lighten);
}

.link-button {
  background-color: transparent;
  border: none;
  cursor: pointer;
  color: var(--primary-color);
}

.link-button:hover {
  color: var(--primary-color-lighten);
}
