header {
  background-color: var(--primary-color);
  color: white;
}

header .container {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  align-items: center;
  flex-wrap: wrap;
}

header {
  padding-left: 1rem;
  padding-right: 1rem;
}

.header {
  display: flex;
  align-items: center;
  box-sizing: border-box;
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  width: 100%;
  height: var(--header-height);
}

.brand,
.brand > a {
  color: white;
  cursor: pointer;
  font-size: 2rem;
  line-height: 1;
  margin: 0;
  padding: 0;
  text-decoration: none;
}

.header .content {
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.nav-toggle {
  cursor: pointer;
}

.nav-toggle > i {
  color: white;
  font-size: 1.5em;
}

.nav {
  display: none;
  flex-direction: column;
  position: fixed;
  top: var(--header-height);
  left: 0;
  width: 100%;
  height: calc(100vh - var(--header-height));
  padding: 2rem;
  background: var(--menu-background);
  font-size: 0.8rem;
  font-weight: 500;
  text-transform: uppercase;
}

#nav-toggle-checkbox:checked ~ .nav {
  display: flex;
}

#nav-toggle-checkbox {
  display: none;
}

.nav-link {
  font-weight: 500;
  padding: 1rem 0;
  text-decoration: none;
  color: white;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.nav-link:hover {
  opacity: 0.8;
}

/* Larger Displays */
@media screen and (min-width: 1280px) {
  .nav-toggle {
    display: none;
  }

  .nav {
    display: flex;
    align-items: center;
    position: static;
    width: unset;
    height: unset;
    padding: unset;
    background: transparent;
    flex-direction: row;
  }

  .nav-link {
    border-bottom: none;
  }

  .nav-link + .nav-link {
    margin-left: 1.5rem;
  }

  .nav .nav-link {
    border-bottom: none;
  }
}
