.search-container {
  padding-top: 1rem;
}

.search-control {
  border-radius: 0.25rem;
  font-size: 1.5rem;
  border: 1px solid rgba(0, 0, 0, 0.5);
  padding: 0.5rem 1rem;
  display: flex;
  align-items: stretch;
  gap: 0.5rem;
}

.search {
  border: none;
  flex: 1 0 0;
  font-size: 1.5rem;
  outline: none;
}

.search-filter-container {
  display: none;
  flex-direction: column;
  gap: 1rem;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  color: white;
  z-index: 1;
  background: var(--menu-background);
  padding: 0.75rem;
}

.search-filter-container.open {
  display: flex;
}

.filter-button {
  display: block;
  position: fixed;
  bottom: 2rem;
  right: 1rem;
  padding: 1rem;
  border-radius: 0.5rem;
  font-size: 1.25rem;
  width: 4rem;
  z-index: 1;
}

.search-bar-container {
  margin-bottom: 1rem;
}

.pager-container + * {
  margin-top: 1rem;
}

.pager-container {
  display: flex;
  justify-content: center;
}

@media screen and (min-width: 768px) {
  .search-container {
    display: grid;
    grid-template-areas:
      "searchBar searchBar searchBar"
      "searchFilter searchResults searchResults"
      "searchFilter searchResults searchResults";
    gap: 1rem;
    grid-template-rows: auto 1fr;
    grid-template-columns: 250px 1fr;
  }

  .search-bar-container {
    grid-area: searchBar;
    margin-bottom: unset;
  }

  .search-filter-container {
    grid-area: searchFilter;
    padding: unset;
    padding-right: 0.5em;
    border-right: 1px solid #333;
    position: relative;
    display: flex;
    background: transparent;
    color: var(--text-color);
    z-index: 0;
  }

  .search-results-container {
    grid-area: searchResults;
  }

  .filter-button {
    display: none;
  }
}
